import React, { Component } from "react";
import AddNote from "./AddNote";
import Note from "./Note";

import { connect } from "react-redux";
import { fetchNotes, addNote } from "../actions";
import "./css/NoteList.css";

class NoteList extends Component {
  state = {
    editing: false
  };

  componentDidMount() {
    this.props.fetchNotes();
  }

  addNote = note => {
    this.props.addNote(note);
  };

  renderNoteList() {
    const { notes } = this.props;

    let viewStyle = {};
    let editStyle = {};

    if (this.state.editing) {
      viewStyle.display = "none";
    } else {
      editStyle.display = "none";
    }

    return (
      notes &&
      notes.map(note => {
        return <Note key={note._id} note={note} />;
      })
    );
  }

  render() {
    const { notes } = this.props;
    return (
      <div>
        <div className="bg-green-100 flex w-64 flex-none min-h-screen flex-col justify-between">
          <div>
            <div className="flex flex-row p-4 mt-4">
              <input
                className=" rounded-lg h-10 w-56 pl-4 focus:outline-none focus:shadow-outline "
                type="text"
                placeholder="Search notes"
              />
              <i className="fas fa-search text-sm text-gray-400 absolute ml-48 mt-4"></i>
            </div>

            <div className="flex flex-col overflow-y-auto">
              <div className="p-4">
                <div className="flex justify-between">
                  <a href="#" className="noteTitle">
                    Title 1
                  </a>
                  <div className="noteDate">17/11/17</div>
                </div>
                <p className="noteTruncatedContent">
                  Lorem ipsum dolor sit ametLorem ipsum dolor sit amet
                </p>
              </div>
            </div>
          </div>

          <div className="flex justify-center mb-10">
            <a href="#" className="btn2 btn2-green">
              Add a note
            </a>
          </div>
        </div>

        {/* 
        <div className="addNote">
          <AddNote addNote={this.addNote} />
        </div>
        <div className={notes.length > 0 ? "collection" : "hidden"}>
          {this.renderNoteList()}
        </div>
        */}
      </div>
    );
  }
}

function mapStateToProps({ notes }) {
  return { notes };
}

export default connect(
  mapStateToProps,
  { fetchNotes, addNote }
)(NoteList);
